<script setup>
window.addEventListener("message", function(event){
  let msg = event.data;
  // console.log("-XXX-", msg)
  if(msg.type == "showVideo"){
    console.log("-showVideo-", msg)
  }
})


const postMessage = () => {
  const el = document.getElementById('webView')
    let msg = {
      'type': 'showVideo_c',
      'uid': 'aaaa',
      'errorCode': 1
    }
    console.log('', el.contentWindow.postMessage);
    el.contentWindow.postMessage((msg), "*")
}

onMounted(() => {
  setTimeout(() => {
    postMessage()
  }, 10000);
})
</script>

<template>
  <iframe id="webView" class="w-100b  h-min-100vh" src="https://5858game.com/telegram/fb/web-mobile/index.html"></iframe>
</template>

